"use client";
import { useCallback, useEffect, useState } from "react";
import { apiFetch } from "@/lib/api";
import { useDebounceValue } from "@/hooks/useDebounceValue";

export function useDepartments() {
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [q, setQ] = useState("");
  const qDebounced = useDebounceValue(q, 250);

  const fetchList = useCallback(async () => {
    setLoading(true);
    try {
      const res = await apiFetch(
        `/api/departments?q=${encodeURIComponent(
          qDebounced
        )}&page=1&limit=200&sortBy=id&sort=asc`
      );
      setItems(res?.data || []);
      setError("");
    } catch (e) {
      setError(e?.data?.error || e?.message || "โหลดไม่สำเร็จ");
      setItems([]);
    } finally {
      setLoading(false);
    }
  }, [qDebounced]);

  useEffect(() => {
    fetchList();
  }, [fetchList]);

  const reload = fetchList;

  const upsert = useCallback(
    async (payload) => {
      await apiFetch(`/api/departments`, {
        method: "POST",
        body: { id: payload.id ?? null, ...payload },
      });
      await reload();
    },
    [reload]
  );

  const remove = useCallback(
    async (id) => {
      if (!confirm("ลบรายการนี้?")) return;
      await apiFetch(`/api/departments/${id}`, { method: "DELETE" });
      await reload();
    },
    [reload]
  );

  return { items, loading, error, q, setQ, reload, upsert, remove };
}
